<template>
  <v-card
    flat
    ripple
    hover
    :color="data.color || 'white'"
    :id="data.id"
    style="max-height:100%;overflow: hidden; border-radius: 4px; border: 1px solid lightgrey !important"
    @click.native="noteClick"
  >
    <v-img
      lazy-src="data:image/png;base64,"
      :src="link"
      height="150"
      v-if="data.attachments[0]"
    >
      <v-toolbar flat card dense color="transparent" v-if="!data.trash">
        <v-spacer></v-spacer>
        <v-btn small icon @click.stop="toggleFaveById(data.id)">
          <v-icon small :color="data.fave ? 'red' : 'grey lighten-2'">mdi-heart</v-icon>
        </v-btn>
      </v-toolbar>
    </v-img>
    <div v-else>
      <v-toolbar flat card dense color="transparent" v-if="!data.trash">
        <div class="font-weight-bold">{{formatTitle()}}</div>
        <v-spacer></v-spacer>
        <v-btn small icon @click.stop="toggleFaveById(data.id)">
          <v-icon small :color="data.fave ? 'red' : 'grey lighten-2'">mdi-heart</v-icon>
        </v-btn>
      </v-toolbar>
    </div>
    <div v-if="data.attachments[0]" class="font-weight-bold">{{formatTitle()}}</div>
    <viewer :value="data.text" :options="editorOptions" class="px-4" @click.native="noteClick" />
  </v-card>
</template>
<script>
import 'tui-editor/dist/tui-editor-extScrollSync'
import 'tui-editor/dist/tui-editor-extColorSyntax'
import 'tui-editor/dist/tui-editor-extUML'
import 'tui-editor/dist/tui-editor-extChart'
import 'tui-editor/dist/tui-editor-extTable'

import 'tui-editor/dist/tui-editor-contents.css'
import 'highlight.js/styles/github.css'
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'

// import 'highlight.js/styles/github.css'
// import hljs from 'highlight.js'
import { mapActions, mapGetters } from 'vuex'
import { UserSession } from 'blockstack'

const md = require('markdown-it')()
  .use(require('markdown-it-highlightjs'))
  .use(require('markdown-it-task-checkbox'))
  .use(require('markdown-it-emoji'))

// window.hljs = hljs

export default {
  props: ['data'],
  components: { Viewer },
  data() {
    return {
      userSession: new UserSession(),
      // hljs: window.hljs,
      link: '',
      editorOptions: {
        usageStatistics: false,
        exts: [
          {
            name: 'chart',
            minWidth: 100,
            maxWidth: 600,
            minHeight: 100,
            maxHeight: 300
          },
          'scrollSync',
          'colorSyntax',
          'uml',
          'mark',
          'table'
        ]
      }
    }
  },
  created() {
    this.getImage()
  },
  computed: {
    ...mapGetters(['filterLabel'])
  },
  methods: {
    ...mapActions([
      'updateActiveNote',
      'toggleTrash',
      'toggleFave',
      'toggleFaveById'
    ]),
    getImage() {
      if (this.data.attachments[0]) {
        this.userSession.getFile(this.data.attachments[0]).then(dataUrl => {
          this.link = dataUrl
        })
      }
    },
    formatTitle() {
      if (this.data.title) {
        if (this.data.title.length >= 20) {
          return this.data.title.substring(0, 18) + '...'
        } else {
          return this.data.title
        }
      } else {
        return 'Untitled Note'
      }
    },
    formatNote(input) {
      if (input) {
        return md.render(input)
        // return marked(input)
      }
    },
    noteClick() {
      this.updateActiveNote(this.data)
    },
    faveNote() {
      this.toggleFave()
    },
    trashNote() {
      this.toggleTrash()
    }
  }
}
</script>
<style>
/* @import '~github-markdown-css'; */
/* @import '~highlight.js/styles/atom-one-dark.css'; */
/* @import '~codemirror/lib/codemirror.css'; */

.v-card--hover:hover {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302),
    0 1px 3px 1px rgba(60, 64, 67, 0.149);
}

.card__text {
  overflow: hide;
}
.task-list-item {
  list-style-type: none;
}
</style>


